@import "../../../css/module/fn";
@import "../../../css/module/color";

#slider {
	margin-top: p2m(40);
	width: 100%;
	height: p2m(840);
	
	.swiper-slide {
		text-align: center;
		font-size: 18px;
		background: #fff;
		width: p2m(530);
		height: p2m(810);
		border-radius: p2m(15);
		
		.cont {
			margin: p2m(15);
			height: p2m(690);
			overflow-y: hidden;
			
			> .contImg {
				width: 100%;
				height: p2m(405);
				display: inline-flex;
				align-items:center;
				justify-content: center;
				> img {
					//background-color: #eee;
					max-height: 100%;
					max-width: 100%;
					padding: p2m(20);
				}
			}
			
			h1 {
				font-size: p2m(28);
				margin: 1em;
				color: #000;
				text: {
					align: left;
				}
				
				.pointer {
					display: inline-block;
					border-radius: p2m(10);
					background-color: $red;
					height: p2m(34);
					line-height: p2m(34);
					padding: 0 0.6em;
					font-size: p2m(16);
					color: #fff;
					float: right;
					.mui-icon {
						font-size: p2m(12);
					}
				}
			}
			p {
				color: $font-black;
				font-size: p2m(22);
				margin: p2m(28);
				text: {
					align: left;
				}
				@include lineClip(1);
			}
		}
		
		.contFoot {
			height: p2m(90);
			background-color: #eee;
			line-height: p2m(90);
			border-bottom: {
				left-radius: p2m(15);
				right-radius: p2m(15);
			}
			color: #888;
			font-size: p2m(20);
			.mui-icon-extra {
				font-size: p2m(20);
			}
		}
		
	}
	
	/*.swiper-slide-active{
		width: p2m(530);
		height: p2m(780);
	}*/
}

.rate {
	margin: p2m(46) auto 0;
	width: p2m(530);
	.rd {
		width: p2m(175);
		height: p2m(175);
		border-radius: p2m(200);
		background-color: #fff;
		
		box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
		
		&#like {
			float: left;
			background: {
				image: url("ico-like.png");
				repeat: no-repeat;
				position: center center;
				size: p2m(95) p2m(81);
			}
		;
		}
		&#unlike {
			float: right;
			background: {
				image: url("ico-hate.png");
				repeat: no-repeat;
				position: center center;
				size: p2m(95) p2m(81);
			}
		;
		}
	}
}

.mui-content {
	height: p2m(990);
	//background: {
	//	image: url("bg-bottom.png");
	//	repeat: no-repeat;
	//	position: center bottom;
	//	size: p2m(550) p2m(64);
	//}
}

.detailContainer {
	
	margin-top: p2m(175);
	
	> .detail {
		background-color: #fff;
		border-radius: p2m(20);
		//height: p2m(870);
		padding-bottom: p2m(25);
		width: p2m(590);
		text-align: center;
		@include shadow();
		
		> .headImg {
			//display: inline-block;
			line-height: p2m(420);
			display: -webkit-inline-flex;
			-webkit-box-align: center;
			-webkit-align-items: center;
			-webkit-justify-content: center;
			display: inline-flex;
			align-items: center;
			justify-content: center;
			width: p2m(545);
			height: p2m(460);
			margin: p2m(-196) auto 0;
			border-radius: p2m(12);
			@include shadow(1, 20px);
			
			background: {
				color: #fff;
				repeat: no-repeat;
				position: center center;
				size: p2m(400) auto;
			}
			> .headThumb {
				max-height: 100%;
				max-width: 100%;
				padding: p2m(80);
			}
		;
		}
		
		> .detailText {
			text-align: left;
			padding: p2m(25);
			height: p2m(500);
			overflow-y: auto;
			overflow-x: hidden;
			
			.pTitle {
				color: #000;
				font-size: p2m(32);
			}
			.pMsg {
				//color: $red;
				word-wrap: break-word;
				font-size: p2m(32);
				margin: p2m(16) 0 p2m(10);
				text-indent: 1.52em;
				//@include lineClip(3);
				background: {
					image: url("/pages/msgBox/assets/ico32-book.png");
					repeat: no-repeat;
					position: left 3px;
					size: p2m(32) p2m(32);
				}
			}
			
			.pTxt {
				color: #555;
				font-size: p2m(32);
				text-indent: 1.52em;
				margin-bottom: p2m(20);
			}
		}
	}
	
}

.resultContainer {
	background-color: #fff;
	border-radius: 20px;
	padding: p2m(70);
	text-align: center;
	@include centered(590, 960);
	@include shadow(0.6);
	
	h2 {
		text-align: center;
		font-size: 16px;
		line-height: p2m(70);
		width: 100%;
		margin: 0 auto;
		border-bottom: 5px double $red;
	}
	
	.imgRs {
		display: block;
		width: p2m(184);
		height: p2m(137);
		margin: p2m(80) auto p2m(50);
	}
	
	.progressBar {
		width: p2m(250);
		height: p2m(8);
		margin: 0 auto;
		background-color: #ddd;
		border-radius: 20px;
		
		.progress {
			background-color: $red;
			height: p2m(8);
			border-radius: 20px;
		}
	}
	
	.res {
		font-size: p2m(70);
		line-height: 1.0em;
		color: $red;
		margin-top: p2m(30);
		margin-bottom: p2m(100);
	}
	
	table {
		width: 100%;
		margin-bottom: p2m(30);
		td {
			text-align: left;
			width: 36%;
			
			&.t {
				color: #999;
				font-size: p2m(26);
			}
			&.r {
				color: #222;
				font-size: p2m(46);
				line-height: p2m(80);
			}
			
			img {
				height: p2m(32);
			}
		}
		
	}
}

div.t {
	color: #999;
	font-size: p2m(26);
}

.btn {
	padding: 3px !important;
}

.tour-backdrop {
	//background-color: transparent !important;
