.continue-buttons {
	position: fixed;
	width: p2m(590);
	left: 50%;
	transform: translate(-50%, -50%);
	top: 98%;
	background-color: red;
	.mui-popup-button {
		color: #fff;
		background-color: #d71711;
		font-size: p2m(26);
		box-shadow: inset 0 3px 3px 0 rgba(0, 0, 0, 0.2);
	}
	.mui-popup-button:first-child:last-child {
		border-radius: 0;
	}
}

